/* Estilo para el fondo del modal */
.ModalBackground {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Fondo oscuro con transparencia */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Asegura que el modal esté por encima de otros elementos */
  opacity: 0; /* Inicialmente oculto */
  animation: fadeIn 0.3s forwards; /* Animación de entrada */
}

/* Estilo para el contenido del modal */
.ModalContent {
  background-color: #ffffff; /* Fondo blanco */
  border-radius: 12px; /* Bordes redondeados */
  padding: 20px;
  max-width: 500px; /* Ancho máximo del modal */
  width: 90%; /* Ajusta el tamaño en pantallas pequeñas */
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3); /* Sombra profunda */
  transform: translateY(-50px); /* Posición inicial para animación */
  animation: slideIn 0.3s ease-out forwards; /* Animación de entrada */
}

/* Botón para cerrar el modal */
.ModalCloseButton {
  position: absolute;
  top: 15px;
  right: 15px;
  background: none;
  border: none;
  font-size: 1.5rem;
  font-weight: bold;
  color: #555; /* Color del ícono */
  cursor: pointer;
  transition: transform 0.2s ease, color 0.2s ease; /* Animaciones al hover */
}

.ModalCloseButton:hover {
  transform: scale(1.2); /* Efecto de agrandamiento */
  color: #ff4d4d; /* Cambia a un rojo suave */
}

/* Animaciones */

/* Aparece el fondo del modal */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Desliza el contenido del modal hacia su posición */
@keyframes slideIn {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Animación de salida (puedes usarla dinámicamente) */
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes slideOut {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    transform: translateY(-50px);
    opacity: 0;
  }
}

/* Estilo para dispositivos pequeños */
@media (max-width: 768px) {
  .ModalContent {
    max-width: 90%; /* Se adapta al ancho de la pantalla */
    padding: 15px; /* Espaciado más pequeño */
  }

  .ModalCloseButton {
    font-size: 1.2rem; /* Botón más pequeño */
  }
}
