.TodoSearch-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}

.TodoSearch {
  padding: 10px;
  font-size: 16px;
  border-radius: 8px;
  border: 1px solid #ccc;
  margin-top: 5px;
  outline: none;
  transition: border-color 0.3s ease;
}

.TodoSearch:focus {
  border-color: #4caf50;
  box-shadow: 0 0 0 2px rgba(76, 175, 80, 0.2);
}

.TodoSearch-label {
  font-size: 14px;
  font-weight: bold;
  color: #333;
}

.TodoSearch-helperText {
  font-size: 12px;
  color: #777;
  margin-top: 5px;
}
