/* Botón principal */
.CreateTodoButton {
  background-color: #61DAFA; /* Color principal */
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5); /* Sombra flotante */
  border: none;
  border-radius: 50%; /* Forma circular */
  cursor: pointer;
  font-size: 2.5rem; /* Tamaño de texto adaptado */
  font-weight: bold;
  color: #FAFAFA; /* Texto claro */
  position: fixed;
  bottom: 2rem; /* Espaciado en pantallas grandes */
  right: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;
  z-index: 100;
  
  /* Transiciones suaves */
  transform: rotate(0);
  transition: transform 0.3s ease, 
              background-color 0.3s ease, 
              box-shadow 0.3s ease, 
              filter 0.3s ease;

  outline: none;
}

/* Efectos de hover */
.CreateTodoButton:hover {
  transform: rotate(224deg); /* Efecto de rotación */
  background-color: #4EC5E9; /* Cambio de color */
  box-shadow: 0px 8px 30px rgba(78, 197, 233, 0.7); /* Sombra más pronunciada */
}

/* Efectos al hacer clic */
.CreateTodoButton:active {
  transform: scale(0.9); /* Botón más pequeño */
  box-shadow: 0px 4px 20px rgba(78, 197, 233, 0.6); /* Sombra más suave */
}

/* Estilo al estar enfocado */
.CreateTodoButton:focus {
  outline: 3px solid #4EC5E9; /* Contorno visible */
  box-shadow: 0 0 10px rgba(78, 197, 233, 0.8); /* Resalta el foco */
  transform: scale(1.05); /* Agranda ligeramente */
}

/* Estilo responsive */
@media (max-width: 768px) {
  .CreateTodoButton {
    bottom: 1.5rem; /* Ajuste de posición en pantallas pequeñas */
    right: 1.5rem;
    height: 56px;
    width: 56px;
    font-size: 2rem; /* Texto más pequeño */
  }
}

@media (max-width: 480px) {
  .CreateTodoButton {
    bottom: 1rem; /* Más espaciado */
    right: 1rem;
    height: 48px;
    width: 48px;
    font-size: 1.8rem; /* Ajuste adicional */
  }
}

