:root {
  --primary-color: #61DAFA;
  --secondary-color: #4CBED8;
  --text-color: #333;
  --font-family: 'Arial', sans-serif;
}

.TodoCounter {
  font-size: 1.5rem; /* Unidades relativas para mejor escalabilidad */
  text-align: center;
  margin: 0 auto;
  padding: 32px 16px; /* Espaciado para un diseño bien proporcionado */
  font-weight: 400;
  color: var(--text-color); /* Uso de variable para color */
  line-height: 1.5;
  max-width: 600px; /* Limita el ancho en pantallas grandes */
  font-family: var(--font-family); /* Definición de la tipografía */
}

.TodoCounter span {
  font-weight: 700;
  color: var(--primary-color); /* Usar color de variable */
  transition: color 0.3s ease, text-decoration 0.3s ease, transform 0.2s ease; /* Añadir transiciones suaves */
}

.TodoCounter span:hover {
  color: var(--secondary-color); /* Color más oscuro en hover */
  text-decoration: underline;
  transform: scale(1.05); /* Ligeramente agrandar el texto al hacer hover */
}

/* Agregar un poco de diseño responsivo */
@media (max-width: 768px) {
  .TodoCounter {
    font-size: 1.2rem; /* Ajustar el tamaño de fuente en pantallas pequeñas */
    padding: 20px 10px; /* Reducir el padding en dispositivos móviles */
  }
}
