/* Contenedor principal para EmptyTodos */
.EmptyTodos-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 24px; /* Espaciado interno generoso */
  text-align: center;
  background-color: #f9fafb; /* Fondo claro y neutro */
  border-radius: 12px; /* Bordes ligeramente más redondeados */
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08); /* Sombra sutil para profundidad */
  max-width: 360px; /* Ajusta el ancho máximo */
  margin: 24px auto; /* Centrado automático y separación */
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Efectos de transición */
}

.EmptyTodos-container:hover {
  transform: translateY(-4px); /* Efecto flotante al pasar el cursor */
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.12); /* Sombra más intensa */
}

/* Mensaje principal */
.EmptyTodos-message {
  font-size: 1.6rem; /* Tamaño de fuente destacado */
  font-weight: 700; /* Texto más marcado */
  color: #2d3748; /* Color gris oscuro para legibilidad */
  margin-bottom: 12px; /* Espaciado inferior */
  text-transform: capitalize; /* Formato de texto */
}

/* Descripción */
.EmptyTodos-description {
  font-size: 1rem; /* Tamaño estándar */
  color: #4a5568; /* Color gris intermedio */
  line-height: 1.5; /* Espaciado entre líneas para mejor legibilidad */
  margin: 0; /* Elimina márgenes adicionales */
  padding: 0 8px; /* Agrega un poco de espacio interno para pantallas pequeñas */
}

/* Diseño responsivo */
@media (max-width: 480px) {
  .EmptyTodos-container {
    padding: 16px; /* Reduce el padding en pantallas pequeñas */
    max-width: 90%; /* Asegura que no ocupe demasiado espacio */
  }

  .EmptyTodos-message {
    font-size: 1.4rem; /* Reduce el tamaño del texto */
  }

  .EmptyTodos-description {
    font-size: 0.9rem; /* Ajusta el texto para dispositivos pequeños */
  }
}
